      format('woff'),
    /* Modern Browsers */
      url('../assets/fonts/open-sans-v34-latin_cyrillic-700.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../assets/fonts/open-sans-v34-latin_cyrillic-700.svg#OpenSans')
      format('svg'); /* Legacy iOS */
}
